import { Link } from "preact-router";

import CardList from "src/components/CardList";
import Card from "src/components/CardList/Card";
import { jobsSortedByStartDate } from "src/data/jobs";
import Footer from "src/components/Footer";
import Section from "src/components/Section";
import { skills } from "./skills";
import classes from "./Expertise.module.css";
import { RouteAwareProps } from "src/declarations";
import { FunctionalComponent } from "preact";
import { AnchorHTMLAttributes } from "preact/compat";

const Anchor = ({ children, ...props }: AnchorHTMLAttributes) => (
  <a {...props}>{children}</a>
);

const renderedJobs = jobsSortedByStartDate.map(
  ([id, { name, position, startDate, endDate }]) => (
    <Card
      Wrapper={Anchor}
      title={`${name} - ${position}`}
      href={`/expertise/${id}`}
      key={id}
    >
      <h3>{name}</h3>
      <h4>
        <b>Position:</b> {position}
      </h4>
      <div>
        <h5>
          <b>
            Started:{" "}
            {startDate
              ? `${startDate.toLocaleString("default", {
                  month: "long",
                  year: "numeric",
                })}`
              : "-"}
          </b>
        </h5>
        <h5>
          <b>
            {endDate
              ? `Ended: ${endDate.toLocaleString("default", {
                  month: "long",
                  year: "numeric",
                })}`
              : "Ongoing"}
          </b>
        </h5>
      </div>
    </Card>
  )
);

const Expertise: FunctionalComponent<RouteAwareProps> = () => {
  return (
    <>
      <Section className={classes["section-expertise"]}>
        <h1>Expertise</h1>
        <hr />
        <h2>Carreer summary</h2>
        <p />
        <CardList>{renderedJobs}</CardList>
        <h2>Skills</h2>
        <p>{skills}</p>
      </Section>
      <Footer className={classes["footer-expertise"]} />
    </>
  );
};

export default Expertise;
